$main_color : #ff0000;

/* mixin - 제일 위에 선언 */
@mixin func() {
  background : #eeeeee;
  padding: 15px;
  border-radius: 5px;
  max-width: 500px;
  width : 100%;
  margin : auto;
}

.red {
  color : $main_color;
}

/* nesting */
/*div.container {
