.my-alert {
  background : #eeeeee;
  padding : 15px;
  border-radius : 5px;
  max-width : 500px;
  width : 100%;
  margin : auto;
  font-weight: bold;
}

/* extend */
.my-alert2 {
  @extend .my-alert;
  background : yellow;
}

/* 함수 사용
.my-alert {
  @include func()
}
 */

.my-alert p {
  margin-bottom : 0;
}

.mar {
  margin : 10px;
}